<template>
  <div class="changeInfo">
    <b-header title="ChangeInfo">
      <div @click="backToHome" class="btn-back">Back to homepage</div>
    </b-header>
    <!-- test -->
    <div id="myPhoto" v-show="personalPhoto">
      <div class="viewPhoto">
        <img :src="avatar" alt="" style="width: 300px;height: 300px" />
      </div>
      <div class="listBox">
        <dl>
          <dt>请上传图片</dt>
          <dd>
            <input type="file" ref="saveImage" name="myphoto" @change="great" />
          </dd>
        </dl>
      </div>
      <div class="save">
        <input type="button" value="保存图片" @click="imageSubmit" />
      </div>
    </div>

