    return {
      personalPhoto: true,
      file: '',
      // imgStr: '',
      errorStr: '',
      avatar: require('assets/userAvatar/15820256995611.jpg'),
      imgUrl: '',
      username: 'molly',
      email: 'molly@hotmail.com',
      uploadFile: '', // 裁剪后的文件
      resultImg: '', // 上传成功，后台返回的路径

      // showOverlayer: false,
      // option: {
      //   img: '',
      //   outputSize: 0.8,
      //   outputType: 'jpeg',
      //   canScale: true,
      //   autoCrop: true, // 是否默认生成截图框
      //   autoCropWidth: 600, // 默认生成截图框宽度
