      //   infoTrue: true, // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      // },
    }
  },
  methods: {
    great(e) {
      const imgFile = e.target.files[0]
      this.file = imgFile
      const fr = new FileReader()
      fr.onload = () => {
        this.avatar = fr.result
      }
      fr.readAsArrayBuffer(imgFile)
      console.log(this.avatar)
    },
    imageSubmit() {
      uploadAvatar(this.file).then((res) => {
        console.log(res)
      })
    },

    // clip image
    onchangeImg(e) {
      const file = e.target.files[0]
      console.log('file1', file)
      // this.option.img = file
      const imgSize = file.size
      console.log(imgSize)
      if (imgSize <= 200 * 1024) {
        // const imgStr = URL.createObjectURL(fileInfo)
        // console.log(imgStr)
        this.$refs.cropper.show(file)
        // var fr = new FileReader()
        // fr.readAsDataURL(file) // 读出 base64

        // fr.onloadend = (e) => {
        //   // 图片的 base64 格式, 可以直接当成 img 的 src 属性值
        //   this.imgUrl = e.target.result
        //   console.log(this.imgUrl)
        //   // this.avatar = dataURL

        // }
      } else {
        console.log('File size is big than 200kb')
        this.errorStr = 'The picure size is too big'
      }
    },
    changeAvatar(file) {
      // 这个是要长传的图片
      console.log(' file 2', file)
      this.avatar = URL.createObjectURL(file)
    },

    save() {},
    backToHome() {
      this.$router.push({ path: '/' })
    },
  },
  components: {
    BHeader,
    // VueCropper,
    AvatarCropper,
  },
}
