import axios from 'axios'

const regist = (email, password) => {
  return axios({
    method: 'post',
    url: '/api/user/regist',
    data: {
      email,
      password,
    },
    headers: {
      'Content-Type': 'application/json',
    },
  })
    .then((res) => {
      console.log(res)
      if (res && res.data) {
        return res.data
      }
      throw new Error('Failed to get the data!')
    })
    .catch((err) => {
      // catch error first before send data to front end
      if (err) {
        console.error(err)
      }
    })
    .then((res) => {
      return Promise.resolve(res) // res could be undefined or data
    })
}

const login = (email, password) => {
  return axios({
    method: 'post',
    url: '/api/user/login',
    data: {
      email,
      password,
    },
    headers: {
      'Content-Type': 'application/json',
    },
  })
    .then((res) => {
      if (res && res.data) {
        return res.data
      }
      throw new Error('Failed to get the data')
    })
    .catch((err) => {
      if (err) {
        console.error(err)
      }
    })
    .then((res) => {
      return Promise.resolve(res)
    })
}

const uploadAvatar = (file) => {
  let param = new FormData()
  // param.append('userId', id)
  param.append('file', file, file.name)
  console.log(param.get('file'))
  return axios({
    method: 'post',
    url: '/api/user/upload-avatar',
    data: param,
    headers: {
      'Content-type': 'multipart/form-data',
    },
  })
    .then((res) => {
      if (res && res.data) {
        return res.data
      }
      throw new Error('Failed to get the data')
    })
    .catch((err) => {
      if (err) {
        console.error(err)
      }
    })
    .then((res) => {
      return Promise.resolve(res)
    })
}

export { regist, login, uploadAvatar }
